import Vue from 'vue'
import Router from 'vue-router'

// Containers
const TheContainer = () => import('@/containers/TheContainer')

// Views
const Openlayers = () => import('@/views/Openlayers')
const Openseadragon = () => import('@/views/Openseadragon')
const Main = () => import('@/views/Main')
const ProductionData = () => import('@/views/ProductionData')
const ExceptionRecords = () => import('@/views/ExceptionRecords')
const EquipMaintain = () => import('@/views/EquipMaintain')
const EquipManage = () => import('@/views/EquipManage')
const AccountManage = () => import('@/views/AccountManage')

// Equipments
const EquipDrawing = () => import('@/views/equipment/equipDrawing')
const EquipForming = () => import('@/views/equipment/equipForming')
const EquipSpooling = () => import('@/views/equipment/equipSpooling')

// Views - Pages
const Page401 = () => import('@/views/pages/Page401')
const Page404 = () => import('@/views/pages/Page404')
const Page500 = () => import('@/views/pages/Page500')
const Login = () => import('@/views/pages/Login')
const Register = () => import('@/views/pages/Register')

// Users
const Users = () => import('@/views/users/Users')
const User = () => import('@/views/users/User')

Vue.use(Router)

const router = new Router({
  mode: 'hash', // https://router.vuejs.org/api/#mode
  linkActiveClass: 'active',
  scrollBehavior: () => ({ y: 0 }),
  routes: configRoutes()
})

