function configRoutes () {
  return [
    {
      path: '/',
      redirect: '/openlayers',
      name: 'Openlayers',
      component: TheContainer,
      children: [
        {
          path: 'openlayers',
          name: 'Openlayers',
          component: Openlayers,
          // meta:{
          //   requiresAuth: true
          // },
          // component: {
          //   render (c) { return c('router-view') }
          // },
          // children: [
          //   {
          //     path: '',
          //     component: Main,
          //   },
          //   {
          //     path: '/main/:mac',
          //     meta: { label: 'Equipment'},
          //     name: 'Equipment',
          //     component: Equipment,
          //   },
          // ]
        },
        // { 
        //   path: '/production-data', 
        //   name: 'Production Data', 
        //   component: ProductionData, 
        //   meta: { requiresAuth: true } 
        // },
        { 
          path: '/openseadragon', 
          name: 'Openseadragon', 
          component: Openseadragon 
        },
        
        // { 
        //   path: '/equip-maintain', 
        //   name: 'Equipment Maintain', 
        //   component: EquipMaintain, 
        //   meta: { requiresAuth: true } 
        // },
        // { 
        //   path: '/equip-manage', 
        //   name: 'Equipment Manage', 
        //   component: EquipManage, 
        //   meta: { requiresAuth: true } 
        // },
        { 
          path: '/account-manage', 
          name: 'Account Manage', 
          component: AccountManage, 
          meta: { requiresAuth: true } 
        },
        {
          path: '/equipment/drawing/:mac',
          name: 'Equipment Drawing',
          component: EquipDrawing,
          meta:{
            requiresAuth: true
          }
        },
        {
          path: '/equipment/forming/:mac',
          name: 'Equipment Forming',
          component: EquipForming,
          meta:{
            requiresAuth: true
          }
        },
        {
          path: '/equipment/spooling/:mac',
          name: 'Equipment Spooling',
          component: EquipSpooling,
          meta:{
            requiresAuth: true
          }
        },
        
        {
          path: 'equip-manage',
          name: 'Equipment Manage',
          component: EquipManage,
          meta:{
            requiresAuth: true
          }
        },
        {
          path: '401',
          name: 'Page401',
          component: Page401
        },
        {
          path: 'users',
          meta: { label: 'Users'},
          component: {
            render (c) { return c('router-view') }
          },
          children: [
            {
              path: '',
              component: Users,
            },
            {
              path: ':id',
              meta: { label: 'User Details'},
              name: 'User',
              component: User,
            },
          ]
        },
      ]
    },
    {
      path: '/pages',
      redirect: '/pages/404',
      name: 'Pages',
      component: {
        render (c) { return c('router-view') }
      },
      children: [
        {
          path: '401',
          name: 'Page401',
          component: Page401
        },
        {
          path: '404',
          name: 'Page404',
          component: Page404
        },
        {
          path: '500',
          name: 'Page500',
          component: Page500
        },
        {
          path: 'login',
          name: 'Login',
          component: Login
        },
        {
          path: 'register',
          name: 'Register',
          component: Register
        }
      ]
    },
    // { path: '/*', redirect: '/pages/login' }
  ]
}

export default router

